	export default {
		data () {
			return {
                ction:'',
                aa:'',
			}
		},
        onLoad(option) {
            this.ction = option.content;
			// this.ction = decodeURIComponent(option.content);
            this.aa=this.ction.replace(/_/g,"=").replace(/\<img/gi, '<img style="max-width:100%;height:auto" ');
        }
	}
